.row{
    width: 100%;
    height: 50px;
    background-color: black;
    margin-bottom: 2rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow-x: auto;
    white-space: nowrap;
    overflow-y: hidden;
    
   > a{
        text-decoration: none;
        color: rgb(16, 251, 255);
        margin: 1rem;
        background-color: rgb(43, 40, 40);
        padding: 5px 10px;
        border-radius: 4px;
        letter-spacing: 1px;
        font-weight: 400;
        box-shadow: 0 0 3px cyan , inset 0 0 3px #00ffbf;
    }
   
}
.row::-webkit-scrollbar{
   width: 0px;
   height: 4px;
}
.row::-webkit-scrollbar-track{
    box-shadow: inset 0 0 5px grey;
}
.row::-webkit-scrollbar-thumb{
    box-shadow: inset 0 0 5px rgb(252, 222, 30);
    border-radius: 30px;
    width: 0px;
}
.row::-webkit-scrollbar{
    display: none;
}


