.heros{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(254, 254, 253);
    border-top:2px solid rgb(86, 85, 85);
    .card{
    max-width: 350px;
    border: 1px solid rgba(62, 37, 138, 0.5);
    box-shadow: 0 0 10px rgba(62, 37, 138, 0.5);
    margin: 100px 20px;
    padding: 20px;
    border-radius: 20px;
    background: #fffa;
    transition: all .2s ease;

        >img{
             width: 100%;
             border: 1px solid rgba(62, 37, 138, 0.5);
             box-shadow: 0 0 10px rgba(62, 37, 138, 0.5);
             
        }
        
        p{
            margin-top: 10px;
            color: #000;
            text-align: justify;
        }
    }
    .card:hover{
        transform: translateY(-10px);
    }
}